.Logout {
    width: 50px;
    height: 50px;
    background-color: #F3F3F3;
    color: #fff;
    border-radius: 15px;
    margin-left: 8px;
    z-index: 99999;
    cursor: pointer;
  
    display: flex;
    justify-content: center;
    align-items: center;
  
    &__image {
      max-width: 18px;
    }
  }